import React, { useEffect, useRef, useState } from 'react';
import { container, visible, heading, paragraph, historyContainer, history, imageWrap } from './HistorySection.module.scss';
import classcat from "classcat";
import fiveImage from '~/images/history/5.png';
import rocketImage from '~/images/history/rocket.png';
import peopleImage from '~/images/history/people.png';
import Br from "~/components/Br/Br";
import { useIntersection } from "use-intersection";
import { animated, useSpring } from "react-spring";

interface HistorySectionProps {}

const formatter = new Intl.NumberFormat('ko');
const constants = {
  anniversary: 5,
  speakers: 60,
  people: 73400,
};
const config = {
  duration: 1800,
};

const HistorySection: React.FC<HistorySectionProps> = () => {
  const ref = useRef();
  const isVisible = useIntersection(ref.current, { once: true, rootMargin: '-200px 0px' });
  const [anniversary, setAnniversary] = useState(0);
  const [speakers, setSpeakers] = useState(0);
  const [people, setPeople] = useState(0);
  const { value: anniversaryValue } = useSpring({ from: { value: 0 }, to: { value: anniversary }, config, delay: 400 });
  const { value: speakersValue } = useSpring({ from: { value: 0 }, to: { value: speakers }, config, delay: 500 });
  const { value: peopleValue } = useSpring({ from: { value: 0 }, to: { value: people }, config, delay: 600});
  useEffect(() => {
    if (isVisible) {
      setAnniversary(constants.anniversary);
      setSpeakers(constants.speakers);
      setPeople(constants.people);
    }
  }, [isVisible]);
  return (
    <div ref={ref} className={classcat([container, isVisible ? visible : ''])} id="event">
      <h2 className={heading}>
           <Br mobile/>  <Br desktop/>
        FEConf <Br mobile/>5 .
      </h2>
      <p className={paragraph}>
        FEConf    <Br mobile/>    <Br desktop/>
          <Br mobile/>     .<Br mobile/> <Br desktop/>
      </p>
      <ul className={historyContainer}>
        <li className={history}>
          <div className={imageWrap}>
            <img src={fiveImage} alt={`${constants.anniversary}`}/>
            <div></div>
          </div>
          <animated.h4>{anniversaryValue.to(val => Math.ceil(val) + '')}</animated.h4>
          <span>FEConf  </span>
        </li>
        <li className={history}>
          <div className={imageWrap}>
            <img src={rocketImage} alt={`${constants.people}`}/>
            <div></div>
          </div>
          <animated.h4>{speakersValue.to(val => Math.floor(val) + '')}</animated.h4>
          <span>5   </span>
        </li>
        <li className={history}>
          <div className={imageWrap}>
            <img src={peopleImage} alt={`${constants.speakers}`}/>
            <div></div>
          </div>
          <animated.h4>{peopleValue.to(val => formatter.format(Math.floor(val)) + '')}</animated.h4>
          <span>5 FEConf  </span>
        </li>
      </ul>
    </div>
  );
}

export default HistorySection;
